





import VueApexCharts from "vue-apexcharts";
import { Component, Vue, Prop } from "vue-property-decorator";
@Component({
  components: {
    apexchart: VueApexCharts,
  },
})
export default class LineChart extends Vue {
  option = {
    stroke: {
      show: true,
      curve: "smooth",
      lineCap: "butt",
      width: 2,
    },
  };

  @Prop({ default: null })
  private data!: Object;
}
